import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, Panel, PanelHeader, Cell, Tabs, List, TabsItem, Group, ListItem, Avatar, ScreenSpinner } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			users: [],
			activeTab: "today",
			spinner: null
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					fetch('https://topbotbackend.herokuapp.com/')
					.then(response => response.json())
					.then(users => {
						users = users.items;
						var tmp =  users.sort((a,b) => a.message_today - b.message_today)
						tmp = tmp.reverse()
						this.setState({
							users: tmp.map((user) => {var tmp = user; tmp.score = tmp.message_today; return tmp;}),
							me: tmp.find(x => x.user_id === e.detail.data.id) ? 
							tmp.find(x => x.user_id === e.detail.data.id).message_today : 0,
							spinner: null
						})
					})
					.catch(err => {
						console.log(err)
						this.setState({spinner: null})
					})
					this.setState({ fetchedUser: e.detail.data });
					break;
					case 'VKWebAppUpdateConfig':
						let schemeAttribute = document.createAttribute('scheme');
						schemeAttribute.value = e.detail.data.scheme ? e.detail.data.scheme : 'client_light';
						document.body.attributes.setNamedItem(schemeAttribute);
					break;
				default:
			}
		});
		this.setState({spinner: <ScreenSpinner />})
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	change = (e) => {
		switch(e.currentTarget.id){
			case 'today':
				var users = this.state.users;
				var tmp =  users.sort((a,b) => a.message_today - b.message_today)
				tmp = tmp.reverse()
				this.setState({
					users: tmp.map((user) => {var tmp = user; tmp.score = tmp.message_today; return tmp;}),
					activeTab: "today",
					me: tmp.find(x => x.user_id === this.state.fetchedUser.id) ? 
						tmp.find(x => x.user_id === this.state.fetchedUser.id).message_today : 0
				})
				break
			case 'week':
				var users = this.state.users;
				var tmp =  users.sort((a,b) => a.message_week - b.message_week)
				tmp = tmp.reverse()
				this.setState({
					users: tmp.map((user) => {var tmp = user; tmp.score = tmp.message_week; return tmp;}),
					activeTab: "week",
					me: tmp.find(x => x.user_id === this.state.fetchedUser.id) ? 
						tmp.find(x => x.user_id === this.state.fetchedUser.id).message_week : 0
				})
				break;
			case 'month':
				var users = this.state.users;
				var tmp =  users.sort((a,b) => a.message_month - b.message_month)
				tmp = tmp.reverse()
				this.setState({
					users: tmp.map((user) => {var tmp = user; tmp.score = tmp.message_month; return tmp;}),
					activeTab: "month",
					me: tmp.find(x => x.user_id === this.state.fetchedUser.id) ? 
						tmp.find(x => x.user_id === this.state.fetchedUser.id).message_month : 0
				})
				break;
			case 'year':
				var users = this.state.users;
				var tmp =  users.sort((a,b) => a.message_year - b.message_year)
				tmp = tmp.reverse()
				this.setState({
					users: tmp.map((user) => {var tmp = user; tmp.score = tmp.message_year; return tmp;}),
					activeTab: "year",
					me: tmp.find(x => x.user_id === this.state.fetchedUser.user_id) ? 
						tmp.find(x => x.user_id === this.state.fetchedUser.id).message_year : 0
				})
				break;	
			default:
		}
	}

	render() {
		let fetchedUser = this.state.fetchedUser
		return (
		<View activePanel={this.state.activePanel} popout={this.state.spinner}>
          <Panel id="home">
            <PanelHeader>WORLD BOTS</PanelHeader>
            <Group>
              <Tabs theme="light">
                <TabsItem
				  onClick={this.change}
				  id="today"
                  selected={this.state.activeTab === 'today'}
                >
                  
                </TabsItem>
                <TabsItem
				  onClick={this.change}
				  id="week"
                  selected={this.state.activeTab === 'week'}
                >
					
                </TabsItem>
				<TabsItem
				  onClick={this.change}
				  id="month"
                  selected={this.state.activeTab === 'month'}
                >
                  
                </TabsItem>
				<TabsItem
				  onClick={this.change}
				  id="year"
                  selected={this.state.activeTab === 'year'}
                >
                  
                </TabsItem>
              </Tabs>
            </Group>
			{fetchedUser &&
			<Group>
				<ListItem
					before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
					description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
					indicator={this.state.me}
				>
					{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
				</ListItem>
			</Group>}
			<Group>
              <List>
				  {
					  this.state.users.map((user,i) => 
					 	<Cell
						 	key={i}
							before={user.photo_200 ? <Avatar src={user.photo_200}/> : <Avatar src="https://vk.com/images/camera_200.png?ava=1" />} 
							indicator={user.score}
						>
							<div style={{display: 'flex'}}>
							{i+1}. {user.first_name+" "+user.last_name}
							</div>
						</Cell> 
					  )
				  }
              </List>
            </Group>
          </Panel>
        </View>
		);
	}
}

export default App;



